.start-screen {
  color: orangered;
  padding: 2rem;
  margin: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 2px orangered;
}

.start-screen main {
  margin: 1rem;
  flex-wrap: wrap;
}

.start-screen button.btn {
  box-shadow: 0 0 2px orangered;
  font-size: 1.2rem;
}

.start-screen button.btn:hover {
  color: black;
  background-color: orangered;
  box-shadow: 0 0 20px orangered;
}
